* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Inter", sans-serif;
  color: #333333;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

html {
  -webkit-text-size-adjust: 100%;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
button,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  line-height: 0;
  touch-action: manipulation;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
  appearance: textfield;
}

table {
  margin-bottom: unset !important;
}

.form-check-input:disabled {
  background-color: #aaaaaa;
  border-color: #aaaaaa;
  opacity: 1;
}

::selection {
  background-color: #575fcc;
  color: #fffcfa;
}

.no-scroll {
  overflow-y: hidden;
}

.color-primary {
  color: #575fcc;
}

.bgc-primary {
  background-color: #575fcc;
}

.btn-primary {
  padding: 10px;
  text-align: center;
  width: fit-content;
  border-radius: 6px;
  display: inline-block;
  border-width: 1px;
  border-style: solid;
  transition: opacity 0.1s ease;
  font-weight: 600;
  font-size: clamp(0.875rem, 0.667rem + 0.889vw, 1rem);
  line-height: 24px;
}
.btn-primary:hover {
  opacity: 0.8;
}
.btn-primary-outline {
  border-color: #575fcc;
}

.heading {
  font-weight: 500;
  color: #333333;
  font-size: clamp(1.25rem, 0.907rem + 1.29vw, 1.875rem);
  line-height: 36px;
}

.title {
  color: #333333;
  font-weight: 600;
  font-size: 18px;
  line-height: 28px;
}

td {
  transition: all 0.24s ease;
}

.button {
  padding: 25px 65px;
  font-weight: 500;
  line-height: 48px;
  font-size: 32px;
}
.button--red {
  background-color: rgb(229, 36, 14);
}
.button--green {
  background-color: rgb(39, 229, 14);
}
.button--green-light {
  background-color: rgb(213, 252, 217);
}
.button--brown {
  background-color: rgb(175, 65, 65);
}
.button--gray {
  background-color: rgb(196, 196, 196);
}

.logo-web {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-family: "Poppins", sans-serif;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  overflow: hidden;
  position: relative;
}
.logo-web::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.7), transparent);
  transform: skewX(-25deg);
  animation: lightRay 5s infinite;
}
@keyframes lightRay {
  0% {
    left: -100%;
  }
  100% {
    left: 100%;
  }
}
.logo-web b {
  font-weight: 700;
  color: #ff4081;
  font-size: 3.5rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}
.logo-web span {
  display: inline-block;
  font-size: 3rem;
  font-weight: 500;
  color: #333;
  text-decoration: none;
  letter-spacing: 2px;
}

.box {
  padding: 35px 30px;
}

.dropdown {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  max-width: 986px;
  width: 100%;
  background-color: #c4c4c4;
}
.dropdown-content {
  padding: 32px 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 20px;
  flex: 1;
  justify-content: space-between;
}
.dropdown-content #value {
  font-size: 32px;
}
.dropdown-option {
  display: none;
}

.message-mobile {
  display: none;
}

@media only screen and (max-width: 767px) {
  #wrapper .message-mobile {
    display: block;
  }
  #wrapper > *:not(.message-mobile) {
    display: none;
  }
}
.header {
  padding: 10px 0 20px 0;
}
.header .wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}
.header .wrapper .logo-web {
  font-weight: 500;
  font-size: 64px;
  line-height: 96px;
  color: #333333;
  transition: color 0.24s ease;
}
.header .wrapper .logo-web:hover {
  color: rgb(175, 65, 65);
}
.header .wrapper img {
  width: 45px;
  cursor: pointer;
}